body {
    font-family: 'Arial', sans-serif; /* Changed to a more modern font */
    margin: 0;
    padding: 0;
    background-image: url("sea.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-size: 1.2em; /* Adjusted for better readability */
    color: #333; /* Darker text for better contrast */
}

/* Header Styles */
header {
    background-color: rgba(148, 141, 141, 0.372); /* Semi-transparent background */
    color: #333;
    padding: 8px 0; /* Added padding for better spacing */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

header nav {
    display: flex;
    justify-content: flex-end;
}

header nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

header nav li {
    margin: 0 30px;
}

header nav a {
    color: #333; /* Dark text for links */
    text-decoration: none;
    font-size: 1.4em; /* Increased font size */
    font-weight: bold;
    padding: 20px;
    transition: color 0.3s ease, transform 0.3s ease; /* Added transform effect */
}

header nav a:hover {
    color: #28a745;
    transform: translateY(-2px);
}

/* About Section Styles */
.about-content {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px; /* Increased padding for better spacing */
}

.about-text {
    flex: 1;
    margin-right: 50px; /* Reduced margin for better alignment */
}

.about-image {
    flex: 1;
}

.oval-image {
    width: 250px;
    height: auto; /* Maintain aspect ratio */
    border-radius: 50%; /* Circular image */
}

/* Expertise Section Styles */
.expertise-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 40px;
    padding: 60px;
}

.expertise-item {
    background-color: #fff; /* White background for items */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Deeper shadow for depth */
    border-radius: 10px; /* Rounded corners */
    padding: 20px;
    text-align: center;
}

.expertise-item h3 {
    margin-top: 0;
}

/* Contact Section Styles */
#contact {
    padding: 60px; /* Adjusted padding for consistency */
}

.contact-form-container {
    display: flex; /* Use flexbox */
    justify-content: center; /* Center the content horizontally */
    padding: 20px; /* Add some padding if needed */
}

#contact form {
    max-width: 500px;
    margin: auto; /* Centered form */
}

#contact input,
#contact textarea {
    width: calc(100% - 22px); /* Adjust width to account for padding/border */
    padding: 10px;
    margin-bottom: 15px;
    border-radius: 5px; 
}

/* Button Styles */
#contact button {
    display: inline-block; /* Ensure the button can be centered */
    background-color: #28a745; /* Fresh green button color */
    color: rgb(15, 14, 14);
    padding: 10px 200px; /* Adjust padding as needed */
    border: none; /* Remove default border */
    border-radius: 5px; /* Match with other inputs */
    cursor: pointer; /* Change cursor on hover */
}

#contact button:hover {
    background-color: red; /* Darker green on hover */
    position: center;
}

/* Footer Styles */
footer {
    background-color: #333; 
    color: white; 
    padding: 20px;
    text-align: center;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
        text-align: center;
    }
}

.expertise-grid {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.expertise-item {
    border: 1px solid #ccc;
    padding: 15px;
    border-radius: 5px;
    width: 30%;
}
